import DialogBox from '@components/DialogBox'
import SubmitButton from '@components/SubmitButton'
import NftGalleryForm from '@pages/NftGallery/NftGalleryForm'
import { showGalleryForm } from '@root/apis/gallery/gallerySlice'
import { RootState } from '@root/store/rootReducers'
import { isMobile } from '@utils/helpers'
import { useEffect, useState } from 'react'
import { useTranslation } from 'react-i18next'
import { useDispatch, useSelector } from 'react-redux'
import { useLocation, useNavigate } from 'react-router-dom'
import CreateNft from './CreateNft'
import NftList from './NftList'

const Nfts = () => {
  const { t } = useTranslation()
  const navigate = useNavigate()
  const dispatch = useDispatch()
  const location: any = useLocation()
  const [status, setStatus] = useState('list')
  const [isLaunch, setIsLaunch] = useState(false)
  const [nftImg, setNftImg] = useState('')
  const isGalleryFormVisible = useSelector(
    (state: RootState) => state.gallery.isGalleryFormVisible,
  )

  const handleNewCreate = () => {
    setNftImg('')
    setStatus('create')
  }
  const handleMyGallery = () => {
    dispatch(showGalleryForm(true))
  }

  useEffect(() => {
    const action = location?.state?.action
    if (action === 'launch') {
      // setStatus('create')
    }
  }, [])

  useEffect(() => {
    if (isGalleryFormVisible) {
      document.body.style.overflow = 'hidden'
    } else {
      document.body.style.overflow = ''
    }
  }, [isGalleryFormVisible])

  const handleOpenGallery = () => {
    setIsLaunch(true)
    dispatch(showGalleryForm(true))
  }

  const handleSetNftImg = (img: string) => {
    setNftImg(img)
    dispatch(showGalleryForm(false))
    setStatus('create')
  }

  return (
    <>
      <DialogBox
        isOpen={isGalleryFormVisible && !isMobile()}
        onClose={() => dispatch(showGalleryForm(false))}
      >
        <NftGalleryForm isLaunch={isLaunch} onSetNftImg={handleSetNftImg} />
      </DialogBox>
      {isGalleryFormVisible && isMobile() ? (
        <NftGalleryForm isLaunch={isLaunch} onSetNftImg={handleSetNftImg} />
      ) : (
        <div className="player-coin dlg-content">
          <div className="nft-tab-title">{t('create your own nft')}</div>
          {status !== 'create' ? (
            <div className="nft-button-box">
              <SubmitButton
                title={t('new nft')}
                className="nft-create-btn"
                onPress={handleNewCreate}
              />
              <SubmitButton
                title={t('my gallery')}
                className="nft-gallery-btn"
                onPress={handleMyGallery}
              />
            </div>
          ) : (
            <></>
          )}
          {status === 'list' ? (
            <NftList />
          ) : status === 'create' ? (
            <CreateNft nftImg={nftImg} onOpenGallery={handleOpenGallery} />
          ) : (
            <></>
          )}
        </div>
      )}
    </>
  )
}

export default Nfts
