import { useState } from 'react'
import SearchIcon from '@mui/icons-material/Search'
import classNames from 'classnames'
import FilterListIcon from '@mui/icons-material/FilterList'
import SearchInput from '@components/Form/SearchInput'
import { Switch } from '@components/Form'
import '@assets/css/components/SearchBar.css'
import { useTranslation } from 'react-i18next'
import { isMobile } from '@utils/helpers'
import { useDispatch, useSelector } from 'react-redux'
import { toggleStakingCoins } from '@root/apis/playerCoins/playerCoinsSlice'
import { RootState } from '@root/store/rootReducers'

type EditFunction = (v: string | undefined) => void
type CloseFunction = (v: boolean | undefined) => void
interface Props {
  isSwitchEnabled?: boolean | null
  isFilterDisabled?: boolean | null
  onEdit: EditFunction
  mode: string
  containerClass?: string
  onClose: CloseFunction
}
const SearchBar: React.FC<Props> = ({
  isSwitchEnabled,
  onEdit,
  mode = '',
  isFilterDisabled,
  containerClass = '',
  onClose,
}) => {
  const playerCoinData = useSelector((state: RootState) => state.playercoins)
  const { isStakingOnlySelected } = playerCoinData
  const { t } = useTranslation()
  const dispatch = useDispatch()
  const [isSearchEnabled, setSearchEnabled] = useState(false)

  const handleClose = () => {
    setSearchEnabled(false)
    onClose(true)
  }

  const handleSearch = () => {
    setSearchEnabled(true)
  }

  const handleToggleSwitch = (checked: boolean) => {
    if (mode === 'wallet') {
      dispatch(toggleStakingCoins(checked))
    }
  }

  return (
    <div
      className={classNames(
        'search-bar',
        containerClass,
        // !isSearchEnabled && isMobile() ? 'p-0' : '',
      )}
    >
      {isSwitchEnabled && !isSearchEnabled ? (
        <Switch
          label={t('staked tokens')}
          onSwitch={handleToggleSwitch}
          isChecked={isStakingOnlySelected}
        />
      ) : (
        <div></div>
      )}
      <div
        className={classNames(
          'search-filter-section',
          !isSearchEnabled ? 'justify-end' : '',
        )}
      >
        {isSearchEnabled ? (
          <SearchInput
            type="text"
            placeholder={t('please enter the search words.')}
            className="in-menu-search"
            onChange={onEdit}
            onClose={handleClose}
          />
        ) : (
          <SearchIcon className="icon-color" onClick={handleSearch} />
        )}
        {!isFilterDisabled && <FilterListIcon />}
      </div>
    </div>
  )
}

export default SearchBar
