import React, { useState, useEffect, useRef } from 'react'
import '@assets/css/components/FlipCard.css'
import { useTranslation } from 'react-i18next'
import classnames from 'classnames'
interface Props {
  title: string
  desc: string
}

const FlipCard: React.FC<Props> = ({ title, desc }) => {
  const [flip, setFlip] = useState(false)
  const { t } = useTranslation()
  const ref = useRef<HTMLDivElement>(null)
  const [height, setHeight] = useState(0)
  useEffect(() => {
    setHeight(ref.current?.clientHeight ?? 0)
  })
  const getTranslation = (text: string) => {
    const translation = t(text)
    if (translation === text) {
      // return '!__no_translation__!'
      return text
    } else {
      return translation
    }
  }

  // const find_overflow_index = (elt: any, str: string) => {
  //   elt.textContent = ''

  //   for (let i = 0; i < str.length; i++) {
  //     elt.textContent += str[i]
  //     if (elt.scrollWidth < elt.offsetWidth) return i
  //   }

  //   return -1 // no overflow
  // }
  // useEffect(() => {
  //   console.log(
  //     'pos',
  //     find_overflow_index(document.getElementById('foo'), desc),
  //   )
  // }, [flip])
  return (
    <div className="flip-card">
      <div
        className={flip ? 'flip-card-inner show' : 'flip-card-inner'}
        onMouseOver={() => setFlip(true)}
        onMouseOut={() => setFlip(false)}
      >
        <div className="flip-card-front">
          <div
            className={classnames(
              'flip-card-title',
              localStorage.getItem('languageName') !== 'English' ? 'mt-30' : '',
            )}
            ref={ref}
          >
            {title}
          </div>
          <div className="flip-card-desc" id="foo">
            {desc.substr(0, height > 96 ? 205 : 272)}
            <span onClick={() => setFlip(true)}>...</span>
          </div>
        </div>
        <div className="flip-card-back" onClick={() => setFlip(false)}>
          <div className="flip-card-desc">
            ...{desc.substr(height > 96 ? 205 : 272)}
          </div>
        </div>
      </div>
    </div>
  )
}

export default FlipCard
