import React, { useEffect, useState } from 'react'
import { useLocation, useNavigate } from 'react-router'
import '@assets/css/pages/PlayerDashboard.css'
import { useTranslation } from 'react-i18next'
import TabGroup from '@components/TabGroup'
import Nfts from './Nfts'
import PlayerCoin from './PlayerCoin'
import Voting from './Voting'
import Drafts from './Drafts'
import PlayerCoinRequest from './PlayerCoinRequest'
import PlayerCoinLaunch from './PlayerCoinLaunch'
import { useDispatch, useSelector } from 'react-redux'
import {
  getSelectedPlayer,
  resetPlayerCoinData,
} from '@root/apis/playerCoins/playerCoinsSlice'
import { RootState } from '@root/store/rootReducers'
import classNames from 'classnames'
import PlayerModal from '@pages/Landing/PlayerModal'
import toast from 'react-hot-toast'
import HotToaster from '@components/HotToaster'

interface Props {
  isOpen?: boolean
}
const PlayerDashboardForm: React.FC<Props> = ({ isOpen }) => {
  const playerStatus = localStorage.getItem('PLAYER_STATUS')
  const { t } = useTranslation()
  const navigate = useNavigate()
  const location: any = useLocation()
  const dispatch = useDispatch()
  const [activeTab, setActiveTab] = useState('player coins')
  const [isNoPlayer, setIsNoPlayer] = useState<number>(0)
  const [playerIndex, setPlayerIndex] = useState<number>(0)
  const [status, setStatus] = useState('')
  const handleGetTab = (tab: string) => {
    setActiveTab(tab)
  }
  const playerCoinData = useSelector((state: RootState) => state.playercoins)
  const {
    allPlayersData,
    tokenExpired,
    isGetPlayerError,
    isGetPlayerSuccess,
    tempAlteration,
    playerCoinActiveTab,
    isGetPlayerSelectionSuccess,
    selectedPlayer,
    isGetSelectedPlayerSuccess,
    isGetSelectedPlayerFailure,
    playerList,
  } = playerCoinData

  const [showPlayerModal, setShowPlayerModal] = useState(false)

  useEffect(() => {
    if (isGetPlayerSelectionSuccess) {
      if (playerList.length > 1) {
        setShowPlayerModal(true)
      }
    } else {
      setShowPlayerModal(false)
    }
  }, [isGetPlayerSelectionSuccess])

  const handlePlayerSelect = (index: number) => {
    dispatch(getSelectedPlayer(playerList[index].playercontract))
  }

  useEffect(() => {
    if (isGetSelectedPlayerSuccess) {
      setShowPlayerModal(false)
    }
    if (isGetSelectedPlayerFailure) {
      toast.error(t('unknown exception'))
    }
  }, [isGetSelectedPlayerSuccess, isGetSelectedPlayerFailure])

  useEffect(() => {
    if (isGetPlayerSuccess) {
      if (allPlayersData.length === 0) {
        console.log('no allplayers')
        setIsNoPlayer(isNoPlayer + 1)
      } else {
        console.log('allplayers foud')
      }
    }
  }, [allPlayersData, isGetPlayerSuccess])

  useEffect(() => {
    window.scrollTo(0, 0)
    console.log({ location })
    console.log('PDFMOUNTED')
    // if (location.pathname !== '/menu') {
    //   dispatch(getPlayerData())
    // }
    const action = location?.state?.action
    if (localStorage.getItem('player')) {
      setPlayerIndex(parseInt(localStorage.getItem('player') ?? '0'))
    }
    return () => {
      dispatch(resetPlayerCoinData())
    }
  }, [])

  useEffect(() => {
    if (playerCoinActiveTab) {
      handleGetTab(playerCoinActiveTab)
    }
  }, [playerCoinActiveTab])

  return (
    <section
      className={classNames(
        'player-dashboard-container',
        !selectedPlayer?.playerstatusid?.playerstatusname
          ? '' //'loader-center'
          : '',
      )}
    >
      <HotToaster />
      {showPlayerModal && (
        <PlayerModal
          isOpen={showPlayerModal}
          playerList={playerList}
          onClose={() => setShowPlayerModal(false)}
          onClick={handlePlayerSelect}
        />
      )}
      {selectedPlayer?.playerstatusid?.playerstatusname === 'Pending' ||
      allPlayersData.length === 0 ||
      (allPlayersData.length === 1 && !selectedPlayer.playerstatusid) ? (
        <PlayerCoinRequest onSubmit={() => setStatus('Launch')} />
      ) : null}
      {selectedPlayer?.playerstatusid?.playerstatusname === 'Verified' ? (
        <PlayerCoinLaunch onSubmit={() => setStatus('Main')} />
      ) : null}
      {selectedPlayer?.playerstatusid?.playerstatusname === 'Deployed' ||
      selectedPlayer?.playerstatusid?.id === 4 ? (
        <div className="main-tabwrapper">
          <TabGroup
            defaultTab={activeTab}
            tabSet={['player coins', 'nfts', 'voting', 'drafts']}
            getSwitchedTab={handleGetTab}
          />
        </div>
      ) : null}
      {(['Deployed', 'Subscription'].includes(
        selectedPlayer?.playerstatusid?.playerstatusname,
      ) ||
        selectedPlayer?.playerstatusid?.id === 4) &&
        activeTab === 'player coins' && <PlayerCoin />}
      {(['Deployed', 'Subscription'].includes(
        selectedPlayer?.playerstatusid?.playerstatusname,
      ) ||
        selectedPlayer?.playerstatusid?.id === 4) &&
        activeTab === 'nfts' && <Nfts />}
      {(['Deployed', 'Subscription'].includes(
        selectedPlayer?.playerstatusid?.playerstatusname,
      ) ||
        selectedPlayer?.playerstatusid?.id === 4) &&
        activeTab === 'voting' && <Voting />}
      {(['Deployed', 'Subscription'].includes(
        selectedPlayer?.playerstatusid?.playerstatusname,
      ) ||
        selectedPlayer?.playerstatusid?.id === 4) &&
        activeTab === 'drafts' && <Drafts />}
    </section>
  )
}

export default PlayerDashboardForm
