import React, { useEffect, useState } from 'react'
import { useTranslation } from 'react-i18next'
import { useDispatch, useSelector } from 'react-redux'
import { RootState } from '@root/store/rootReducers'
import { resetDraftNewPlayers } from '@root/apis/playerCoins/playerCoinsSlice'
import toast from 'react-hot-toast'
import ResponseAlert from '@components/ResponseAlert'
import classNames from 'classnames'
import { callWeb3Method } from '@utils/helpers'
import BottomPopup from '@components/BottomPopup'
import SubmitButton from '@components/SubmitButton'
import MetamaskIcon from '@assets/icons/icon/metamask.svg'
import CoinbaseIcon from '@assets/icons/icon/coinbase.svg'

interface Props {
  promptText: string
  txnHash: string
  onClose: (value?: any) => void
  onSuccess: (value?: any) => void
  walletAddress: string
  errorMsg: string
  isPromptLoading?: boolean
  operationMode?: string
}

const style = {
  height: 30,
  border: '1px solid green',
  margin: 6,
  padding: 8,
}

const Web3ActionPrompt: React.FC<Props> = ({
  onClose,
  onSuccess,
  walletAddress,
  promptText,
  txnHash,
  errorMsg,
  isPromptLoading = false,
  operationMode = 'add',
}) => {
  const walletType = localStorage.getItem('wallet')
  const { t } = useTranslation()
  const dispatch = useDispatch()
  // const [txnHash, setTxnHash] = useState('')
  const [isLoading, setLoading] = useState(false)

  const playerCoinData = useSelector((state: RootState) => state.playercoins)
  const { player1contract, player1contractabi } = playerCoinData

  // useEffect(() => {
  //   return () => {
  //     setTxnHash('')
  //   }
  // }, [])

  useEffect(() => {
    return () => {
      dispatch(resetDraftNewPlayers())
    }
  }, [])

  useEffect(() => {
    console.log({ operationMode })
  }, [operationMode])

  useEffect(() => {
    setLoading(isPromptLoading)
  }, [isPromptLoading])

  const handleInitRemoveAdminPlayer = () => {
    // setLoading(true)
    // const promise = callWeb3Method(
    //   // methodName: string,
    //   'setPlayerRole',
    //   player1contract,
    //   player1contractabi,
    //   [walletAddress],
    // )
    // promise
    //   .then((txn: any) => {
    //     setTxnHash(txn.hash)
    //     setLoading(false)
    //   })
    //   .catch((err: any) => {
    //     setLoading(false)
    //     toast.error(err.reason || err.message)
    //     onClose()
    //   })
    setLoading(true)
    onSuccess()
    return
    setLoading(true)
    const promise = callWeb3Method(
      'revokePlayerRole',
      player1contract,
      player1contractabi,
      [walletAddress],
    )
    promise
      .then((txn: any) => {
        console.log('removeAdmintxn---', txn)
        // setTxnHash(txn.hash)
        setLoading(false)
      })
      .catch((err: any) => {
        console.log('removeAdminErr---', err)
        setLoading(false)
        if (err.message === '406') {
          toast.error(t('this functionality unavailable for internal users'))
        } else {
          toast.error(err.reason || err.message)
        }
        // toast.error(err.reason || err.message)
        onClose()
      })
  }

  const cancelRemoveAdminPlayer = () => {
    onClose()
  }

  return (
    <section className="new-draft">
      <BottomPopup
        mode={classNames(
          'wallet drafting-players-loader',
          !isPromptLoading ? 'vertical-flex' : '',
        )}
        isOpen={isLoading || txnHash.length > 0}
      >
        {!isPromptLoading ? (
          <img
            loading="lazy"
            src={walletType === 'Metamask' ? MetamaskIcon : CoinbaseIcon}
            alt="metamask-icon"
          />
        ) : null}
        {isLoading && !txnHash && !errorMsg ? (
          <div className="checkout-loader-wrapper">
            {!isPromptLoading ? (
              <div className="input-label mt-10">
                {t('awaiting approval from') + ' ' + walletType}
              </div>
            ) : null}
            <div className="loading-spinner">
              <div className="spinner__circle">
                <div className="spinner__circle-gradient"></div>
                <div className="spinner__circle-inner"></div>
              </div>
            </div>
          </div>
        ) : (
          <>
            {txnHash ? (
              <div
                className={classNames(
                  'purchase-submit-wrapper',
                  'btn-purchase',
                  'web3action-success',
                )}
              >
                <ResponseAlert status={'Success'} />
                {txnHash && (
                  <a
                    className="tx-link"
                    href={'https://mumbai.polygonscan.com/tx/' + txnHash}
                    target="_blank"
                  >
                    {t('show transaction')}
                  </a>
                )}
              </div>
            ) : (
              <div className="txn-err-wrapper">
                <ResponseAlert status={'Error'} />
                <div className="input-feedback">{errorMsg}</div>
              </div>
            )}

            <div className="bottom-close">
              <div className="close-button" onClick={onClose}>
                {t('close')}
              </div>
            </div>
          </>
        )}
      </BottomPopup>
      <div className="remove-adminplayer-prompt-wrapper">
        <div className="new-draft-title">{promptText}</div>
        <div
          className={classNames(
            'new-draft-title eth-address',
            operationMode === 'add' ? 'success' : 'error',
          )}
        >
          {walletAddress}
        </div>
        <div className="mt-10">
          <SubmitButton
            title={t('yes')}
            className="m-0auto mt-20"
            onPress={() => handleInitRemoveAdminPlayer()}
          />
          <SubmitButton
            title={t('no')}
            className="m-0auto mt-20 btn-disabled"
            onPress={() => cancelRemoveAdminPlayer()}
          />
        </div>
      </div>
    </section>
  )
}

export default Web3ActionPrompt
