/* eslint-disable no-unused-vars */
import React, { useRef, useEffect } from 'react'
import Modal from 'react-modal'
import classnames from 'classnames'
import { isMobile } from '@utils/helpers'
import '@assets/css/components/DialogBox.css'
interface Props {
  formActive?: string
  isOpen: boolean
  children: React.ReactNode
  contentClass?: string
  closeBtnClass?: string
  parentClass?: string
  isMandatory?: boolean
  onClose: (v?: any) => void
}

Modal.setAppElement('#root')

const WalletDialog: React.FC<Props> = ({
  isOpen,
  children,
  contentClass,
  closeBtnClass,
  parentClass,
  isMandatory = false,
  onClose,
}) => {
  const ref: any = useRef(null)
  let span: any = null
  useEffect(() => {
    span = ref.current // corresponding DOM node
  }, [])

  useEffect(() => {
    console.log('isopenrrr--', isOpen)
    console.log('spanclass--', span.className)
    if (isOpen) {
      // const span = ref.current // corresponding DOM node
      span.className += ' show'
    }
  }, [isOpen])

  const handleClose = () => {
    // const span = ref.current // corresponding DOM node
    // span.className = 'modal ' + parentClass
    onClose()
  }

  return (
    <div
      id="walletModal"
      ref={ref}
      // className={classnames('modal', isOpen ? 'show' : '', parentClass)}
      className={classnames('modal', parentClass)}
    >
      <div className={classnames('modal-content', contentClass)}>
        {!isMandatory ? (
          <button
            className={classnames('close', isMobile() ? closeBtnClass : '')}
          >
            {/* <span onClick={onClose}>&times;</span> */}
            <span onClick={() => handleClose()}>&times;</span>
          </button>
        ) : null}
        {children}
      </div>
    </div>
  )
}

export default WalletDialog
