import React, { useEffect, useState } from 'react'
import { AppLayout } from '@components/index'
import TabGroup from '@components/TabGroup'
import { setPurchaseMode } from '@root/apis/purchase/purchaseSlice'
import { useNavigate, useLocation } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { callWeb3Method, isMobile } from '@utils/helpers'
import '@assets/css/pages/PurchaseNft.css'
import { useTranslation } from 'react-i18next'
import { RootState } from '@root/store/rootReducers'
import NewDraft from './NewDraft'
import {
  fetchDraftNewPlayers,
  getPlayer2Contract,
  getPlayerDrafts,
  persistDrafteePlayer,
} from '@root/apis/playerCoins/playerCoinsSlice'
import Web3ActionPrompt from '../PlayerCoin/web3Actionprompt'
import toast from 'react-hot-toast'

const Web3ActionPage: React.FC = () => {
  const { t } = useTranslation()
  const navigate = useNavigate()
  const dispatch = useDispatch()
  const location: any = useLocation()
  const [promptDialog, setPromptDialog] = useState(
    t('are you sure you want to add this player as draftee'),
  )
  const [web3Action, setWeb3Action] = useState('')
  const [promptLoading, setPromptLoading] = useState(false)
  const [txnErr, setTxnErr] = useState('')
  const [txnHash, setTxnHash] = useState('')
  const playerCoinData = useSelector((state: RootState) => state.playercoins)
  const {
    drafteePlayerUrl,
    isGetPlayer2ContractSuccess,
    isGetPlayerContractError,
    player2contract,
    player1contract,
    player1contractabi,
  } = playerCoinData

  useEffect(() => {
    window.scrollTo(0, 0)
    if (drafteePlayerUrl) {
      setPromptLoading(true)
      dispatch(getPlayer2Contract(drafteePlayerUrl))
    } else {
      navigate(-1)
    }
    // window.history.replaceState(null, 'Buy', '/')
  }, [])

  useEffect(() => {
    if (isGetPlayer2ContractSuccess) {
      setPromptDialog(t('are you sure you want to add this player as draftee'))
      setWeb3Action('addDraftee')
      setPromptLoading(false)
      // if (loginInfo) {
      // buyforExternalWallet()
      // }
      // else if (loginId) {
      //   buyforInternalWallet()
      // }
    }
    if (isGetPlayerContractError) {
      //   toast.error(t('Loading player contract is failed.'))
      setTxnErr(t('Loading player contract is failed.'))
    }
    // setIsLoadingMatic(false)
    // setIsDisabledMethod(false)
  }, [isGetPlayer2ContractSuccess, isGetPlayerContractError])

  const handleActionSuccess = async () => {
    console.log({ web3Action })
    buyforExternalWallet()
  }

  const buyforExternalWallet = async () => {
    const promise = callWeb3Method(
      // methodName: string,
      'createDraftRequest',
      player1contract, // "0xb6000dffb543008bd7f9cda2615608828f0bf904"
      player1contractabi, // abi of player 1 contract
      [player2contract], // 0xa1eff55865575ddf81c2c5727490524dd56da5e4
    )
    promise
      .then((txn: any) => {
        setTxnHash(txn.hash)
        // setLoading(false)
      })
      .catch((err: any) => {
        // setLoading(false)
        if (err.message === '406') {
          // toast.error(t('this functionality unavailable for internal users'))
          setTxnErr(t('this functionality unavailable for internal users'))
        } else {
          // toast.error(err.reason || err.message)
          setTxnErr(err.reason || err.message)
        }
      })
  }

  const handleRemoveAdminPrompt = async () => {
    // setShowPropmt(false)
    await dispatch(persistDrafteePlayer({ playerUrl: '' }))
    setTxnErr('')
    setTxnHash('')
    setPromptDialog('')
    setWeb3Action('')
    navigate(-1)
  }

  return (
    <AppLayout
      headerStatus="header-status"
      headerClass="home"
      footerStatus="footer-status"
    >
      <section className="player-dashboard-container">
        <Web3ActionPrompt
          txnHash={txnHash}
          isPromptLoading={promptLoading}
          errorMsg={txnErr}
          promptText={promptDialog}
          onSuccess={handleActionSuccess}
          onClose={handleRemoveAdminPrompt}
          walletAddress={player2contract}
          operationMode="add"
        />
      </section>
    </AppLayout>
  )
}

export default Web3ActionPage
