import React, { useEffect } from 'react'
import { AppLayout } from '@components/index'
import PlayerDashboardForm from './PlayerDashboardForm'
import TagManager from 'react-gtm-module'
import { tagManagerArgs } from '@root/constants'
import { useDispatch, useSelector } from 'react-redux'
import { getPlayerData } from '@root/apis/playerCoins/playerCoinsSlice'
import { RootState } from '@root/store/rootReducers'

const PlayerDashboard: React.FC = () => {
  const dispatch = useDispatch()

  useEffect(() => {
    // window.history.replaceState(null, 'Buy', '/')
    TagManager.initialize(tagManagerArgs)
  }, [])

  useEffect(() => {
    if (location.pathname !== '/menu') {
      dispatch(getPlayerData())
    }
  }, [])

  const { allPlayersData } = useSelector(
    (state: RootState) => state.playercoins,
  )

  return (
    <AppLayout
      headerStatus="header-status"
      headerClass="home"
      footerStatus="footer-status"
      // footerStatus={
      //   allPlayersData.length === 0 ||
      //   ['Pending', 'Verified'].includes(
      //     allPlayersData[0]?.playerstatusid?.playerstatusname,
      //   )
      //     ? 'footer-status'
      //     : ''
      // }
    >
      <PlayerDashboardForm />
    </AppLayout>
  )
}

export default PlayerDashboard
