import React, { useState } from 'react'
import '@assets/css/components/ClaimCard.css'
import EditOutlinedIcon from '@mui/icons-material/EditOutlined'
import DeleteForeverOutlinedIcon from '@mui/icons-material/DeleteForeverOutlined'

interface Props {
  id: string
  title: string
  desc: string
  editable: boolean
  isClaim: boolean
  onEdit: (id: string, title: string, desc: string, isClaim: boolean) => any
}

const ClaimCard: React.FC<Props> = ({
  id,
  title,
  desc,
  editable,
  isClaim,
  onEdit,
}) => {
  const [showIcons, setShowIcons] = useState(false)
  const handleMouseOver = () => {
    if (editable) {
      setShowIcons(true)
    }
  }
  const handleMouseOut = () => {
    if (editable) {
      setShowIcons(false)
    }
  }

  return (
    <div
      className="claim-card"
      onMouseOver={handleMouseOver}
      onMouseLeave={handleMouseOut}
    >
      {showIcons && (
        <div className="claim-card-icons" onClick={handleMouseOut}>
          <EditOutlinedIcon onClick={() => onEdit(id, title, desc, isClaim)} />
        </div>
      )}
      <div className="claim-card-title">{title}</div>
      <div className="claim-card-desc">{desc}</div>
    </div>
  )
}

export default ClaimCard
