import React, { useEffect, useState } from 'react'
import { AppLayout } from '@components/index'
import TabGroup from '@components/TabGroup'
import { setPurchaseMode } from '@root/apis/purchase/purchaseSlice'
import { useNavigate, useLocation } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { isMobile } from '@utils/helpers'
import '@assets/css/pages/PurchaseNft.css'
import { useTranslation } from 'react-i18next'
import { RootState } from '@root/store/rootReducers'
import NewDraft from './NewDraft'
import {
  fetchDraftNewPlayers,
  getPlayer2Contract,
  getPlayerDrafts,
} from '@root/apis/playerCoins/playerCoinsSlice'

const NewDraftPage: React.FC = () => {
  useEffect(() => {
    window.scrollTo(0, 0)
    // window.history.replaceState(null, 'Buy', '/')
  }, [])
  const { t } = useTranslation()
  const navigate = useNavigate()
  const dispatch = useDispatch()
  const location: any = useLocation()

  // const handleDraftClose = () => {
  //   console.log('closedraft')
  // }

  const handleDraftClose = (value: any) => {
    if (value?.txnCompleted) {
      dispatch(getPlayerDrafts())
    } else if (value?.playerUrl) {
      console.log('kkk', value?.playerUrl)
      // setShowPropmt(true)
      // setPromptLoading(true)
      dispatch(getPlayer2Contract(value?.playerUrl))
    } else if (!value?.playerUrl) {
      // setShowPropmt(false)
    }
    // setShowNewDraftPopup(false)
  }

  useEffect(() => {
    dispatch(fetchDraftNewPlayers({ status_id: [3, 4] }))
  }, [])

  return (
    <AppLayout
      headerStatus="header-status"
      headerClass="home"
      footerStatus="footer-status"
    >
      <section className="players-buy">
        <NewDraft onClose={handleDraftClose} />
      </section>
    </AppLayout>
  )
}

export default NewDraftPage
