import React, { useState, useEffect, useRef, memo } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useTranslation } from 'react-i18next'
import { NumberFormat } from '@root/utils/NumberFormat'
import { IPlayerCard as CardProps } from '@root/types'
import { useNavigate } from 'react-router-dom'
import { setPurchasePlayerId } from '@root/apis/purchase/purchaseSlice'
import classnames from 'classnames'
import '@assets/css/components/PlayerCard.css'
import maticIcon from '@assets/images/matic-token-icon.png'
import dollarIcon from '@assets/images/dollar_mecarreira.png'
import { RootState } from '@root/store/rootReducers'
import { truncateDecimals } from '@utils/helpers'
import TooltipLabel from '@components/TooltipLabel'
import { PLAYER_STATUS } from '@root/constants'
import PlayerImage from '@components/PlayerImage'
import 'flag-icons/css/flag-icons.min.css'
interface Props {
  card: CardProps
  isPlayerDetails?: boolean
  onBuy: any
  onSell: any
}

const refObj = {
  '24h_change': '',
  coin_issued: '',
  holders: '',
  id: '',
  matic: undefined,
}

const Card: React.FC<Props> = ({
  card,
  onBuy,
  onSell,
  isPlayerDetails = false,
}) => {
  const { t } = useTranslation()
  const navigate = useNavigate()
  const dispatch = useDispatch()
  const [profileHovered, setProfileHovered] = useState(false)
  const [buttonHovered, setButtonHovered] = useState(false)
  let countDown: any = null
  const now = new Date()
  const countDownDate = new Date(
    now.setDate(now.getDate() + card.playercontractsubscriptionstart),
  ).getTime()
  const [state, setState] = useState({
    days: '00',
    hours: '00',
    minutes: '00',
    seconds: '00',
  })
  const activateComingSoon =
    card.playerstatusid === PLAYER_STATUS.COMINGSOON && false

  const updateState = (data: any) => {
    setState(state => ({ ...state, ...data }))
  }

  const initCountDown = () => {
    countDown = setInterval(function () {
      const now = new Date().getTime()
      const distance = countDownDate - now
      const days = Math.floor(distance / (1000 * 60 * 60 * 24))
      const hours = Math.floor(
        (distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60),
      )
      const minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60))
      const seconds = Math.floor((distance % (1000 * 60)) / 1000)

      if (distance < 0) {
        clearInterval(countDown)
        updateState({
          days: '00',
          hours: '00',
          minutes: '00',
          seconds: '00',
        })
      } else {
        updateState({
          days,
          hours,
          minutes,
          seconds,
        })
      }
    }, 1000)
  }

  useEffect(() => {
    if (card.playercontractsubscriptionstart > 0) {
      initCountDown()
    }
    return () => {
      clearInterval(countDown)
    }
  }, [])

  function usePrevious(value: any) {
    const ref = useRef<any>(refObj)
    useEffect(() => {
      ref.current = value
    })
    return ref.current
  }
  const prevAmount = usePrevious(card)

  const handleClick = () => {
    const player = card.detailpageurl //card.name.toLocaleLowerCase().replaceAll(' ', '-')
    if (!isPlayerDetails) {
      navigate(`player/${player}`, {
        state: { id: card.profileLink, detaultPageUrl: card.detailpageurl },
      })
    } else {
      navigate(`/player/${player}`, {
        state: { id: card.profileLink, detaultPageUrl: card.detailpageurl },
      })
      location.reload()
    }
  }

  const playerStatsData = useSelector((state: RootState) => state.playerstats)
  const { fetchPlayerStatsRateData } = playerStatsData

  const handleBuy = (e: any) => {
    e.stopPropagation()
    const profileLink = `player/${card.id}`
    dispatch(setPurchasePlayerId(profileLink))
    onBuy()
  }

  const handleSell = (e: any) => {
    e.stopPropagation()
    const profileLink = `player/${card.id}`
    dispatch(setPurchasePlayerId(profileLink))
    onSell()
  }

  const handleProfileHover = (value: boolean) => (evt: any) => {
    setProfileHovered(value)
  }

  const handleButtonHover = (value: boolean) => (evt: any) => {
    setButtonHovered(value)
  }

  const getUsdFromMatic = (maticValue: number) => {
    let currency = null
    if (fetchPlayerStatsRateData) {
      currency = maticValue * fetchPlayerStatsRateData.rate
    } else {
      currency = maticValue * card?.exchangeRateUSD?.rate
    }
    if (!isNaN(currency)) {
      return truncateDecimals(currency, 4)
    }
    return '00.0000'
  }

  function getPercentageChange(
    oldNumber: number,
    newNumber: number,
    time: string,
  ) {
    const decreaseValue = oldNumber - newNumber
    const percentChange = (decreaseValue / oldNumber) * 100
    if (!isNaN(percentChange)) {
      return percentChange.toFixed(2)
    }
    return '00.00'
  }

  return (
    <>
      <div
        className={classnames(
          'card',
          'coming-soon-wrapper',
          activateComingSoon && 'coming-soon-card-bg',
        )}
        onClick={handleClick}
        onMouseEnter={handleProfileHover(true)}
        onMouseLeave={handleProfileHover(false)}
      >
        {activateComingSoon ? (
          <div
            className="coming-soon h-3"
            style={{
              visibility:
                card.playercontractsubscriptionstart === 0
                  ? 'visible'
                  : 'hidden',
            }}
          >
            {t('coming soon')}
          </div>
        ) : (
          <div
            className="inside-card"
            onMouseEnter={handleProfileHover(true)}
            onMouseLeave={handleProfileHover(false)}
          >
            <div className="matic-wrapper">
              <div
                className={classnames(
                  'number-color',
                  'matic-value',
                  'matic-figure',
                  !prevAmount?.matic || prevAmount?.matic === card.matic
                    ? ''
                    : card.matic > prevAmount.matic
                    ? 'profit'
                    : 'loss',
                )}
              >
                {truncateDecimals(card.matic, 7) || '00.00'}
              </div>
              <TooltipLabel title="MATIC">
                <img src={maticIcon} alt="" />
              </TooltipLabel>
            </div>
            <div className="matic-wrapper">
              <div
                className={classnames(
                  'number-color',
                  'pt-5',
                  'usd-price',
                  !prevAmount?.matic || prevAmount?.matic === card.matic
                    ? ''
                    : getUsdFromMatic(card.matic) >
                      getUsdFromMatic(prevAmount.matic)
                    ? 'profit'
                    : 'loss',
                )}
              >
                ${getUsdFromMatic(card.matic)}
              </div>
            </div>
          </div>
        )}
        <div
          className="time capitalize m-0"
          onMouseEnter={handleProfileHover(true)}
          onMouseLeave={handleProfileHover(false)}
        >
          &nbsp;
        </div>
        <div
          onMouseEnter={handleProfileHover(true)}
          onMouseLeave={handleProfileHover(false)}
        >
          <div
            className={classnames(
              'img',
              profileHovered && !buttonHovered ? 'profile-hovered-img' : '',
            )}
          >
            <PlayerImage
              src={card.playerpicture || card.img}
              className="img-radius cache-img"
            />
          </div>
          <div
            className={classnames(
              'name player-title-name',
              profileHovered ? 'player-name-active' : '',
            )}
          >
            {card.name}
            <span className="fi fi-es"></span>
          </div>
          {!activateComingSoon ? (
            <>
              <div className="link">
                <div
                  className={classnames(
                    'link-icon',
                    profileHovered && !buttonHovered ? 'link-icon-hovered' : '',
                  )}
                ></div>
              </div>
              <div className="divide mt-10"></div>
              <div className="changed-price">
                <div>{t('price change 24h')}</div>
                <div
                  className={classnames(
                    'number-color',
                    // !prevAmount['24h_change'] && !prevAmount.matic
                    //   ? ''
                    //   : getPercentageChange(
                    //       card['24h_change'],
                    //       card['matic'],
                    //       'current',
                    //     ) >
                    //     getPercentageChange(
                    //       prevAmount['24h_change'],
                    //       prevAmount['matic'],
                    //       'prev',
                    //     )
                    //   ? 'profit'
                    //   : 'loss',
                    !prevAmount?.matic || prevAmount?.matic === card.matic
                      ? ''
                      : getPercentageChange(
                          card['24h_change'],
                          card['matic'],
                          'current',
                        ) >
                        getPercentageChange(
                          prevAmount['24h_change'],
                          prevAmount['matic'],
                          'prev',
                        )
                      ? 'profit'
                      : 'loss',
                  )}
                >
                  {getPercentageChange(
                    card['24h_change'],
                    card['matic'],
                    'current',
                  ) + '%'}
                </div>
              </div>
              <div className="coin-issued">
                <div>{t('coin issued')}</div>
                <div
                  className={classnames(
                    'number-color',
                    !prevAmount?.coin_issued ||
                      prevAmount?.coin_issued === card.coin_issued
                      ? ''
                      : card.coin_issued > prevAmount.coin_issued
                      ? 'profit'
                      : 'loss',
                  )}
                >
                  {/* {NumberFormat(card.coin_issued)} */}
                  {card.coin_issued}
                </div>
              </div>
              <div className="holders">
                <div>{t('holders')}</div>
                <div
                  className={classnames(
                    'number-color',
                    !prevAmount?.holders || prevAmount?.holders === card.holders
                      ? ''
                      : card.holders > prevAmount.holders
                      ? 'profit'
                      : 'loss',
                  )}
                >
                  {/* {NumberFormat(card.holders)} */}
                  {card.holders}
                </div>
              </div>
            </>
          ) : (
            card.playercontractsubscriptionstart > 0 && (
              <div className="coming-soon-description">{t('launching in')}</div>
            )
          )}
        </div>
        {!activateComingSoon ? (
          <div className="bottom-part">
            <div
              className="buy"
              onClick={handleBuy}
              onMouseEnter={handleButtonHover(true)}
              onMouseLeave={handleButtonHover(false)}
            >
              {t('buy')}
            </div>
            <div
              className="sell"
              onClick={handleSell}
              onMouseEnter={handleButtonHover(true)}
              onMouseLeave={handleButtonHover(false)}
            >
              {t('sell')}
            </div>
          </div>
        ) : (
          card.playercontractsubscriptionstart > 0 && (
            <div className="coming-soon-time-card">
              <div className="timer">
                <div className="heading">{t('days')}</div>
                <div className="time">
                  {~~state.days < 10 && 0}
                  {state.days}
                </div>
              </div>
              :
              <div className="timer">
                <div className="heading">{t('hours')}</div>
                <div className="time">
                  {~~state.hours < 10 && 0}
                  {state.hours}
                </div>
              </div>
              :
              <div className="timer">
                <div className="heading">{t('mins')}</div>
                <div className="time">
                  {~~state.minutes < 10 && 0}
                  {state.minutes}
                </div>
              </div>
              :
              <div className="timer">
                <div className="heading">{t('secs')}</div>
                <div className="time">
                  {~~state.seconds < 10 && 0}
                  {state.seconds}
                </div>
              </div>
            </div>
          )
        )}
      </div>
    </>
  )
}

export default memo(Card)
