const isValid = (
  methodName: string,
  player1contract: string,
  player1contractabi: any,
) => {
  let validTest = {}

  if (!methodName) {
    validTest = { ...validTest, isError: t('method name is required') }
  } else if (!player1contract) {
    validTest = {
      ...validTest,
      isError: t('host contract address is required'),
    }
  } else if (!player1contractabi) {
    validTest = { ...validTest, isError: t('host contract abi is required') }
  }
  return validTest
}

interface validationInstanceObj {
  isError?: string
}

export const callWeb3Method = (
  methodName: string,
  player1contract: string,
  player1contractabi: any,
  inputPrams: string[], // 2nd player contract adddress
) => {
  console.log({
    methodName,
    player1contract,
    player1contractabi,
    inputPrams,
  })
  const isValidInstace: validationInstanceObj = isValid(
    methodName,
    player1contract,
    player1contractabi,
  )
  // const provider = new ethers.providers.Web3Provider(window.ethereum)
  let walletProvider = window.ethereum?.providers?.find((provider: any) =>
    localStorage.getItem('wallet') === 'Metamask'
      ? provider.isMetaMask
      : localStorage.getItem('wallet') === 'Coinbase'
      ? provider.isCoinbaseWallet
      : provider.isTrustWallet,
  )
  if (!walletProvider) {
    if (
      (window.ethereum?.isMetaMask &&
        localStorage.getItem('wallet') === 'Metamask') ||
      (window.ethereum?.isCoinbaseWallet &&
        localStorage.getItem('wallet') === 'Coinbase')
    ) {
      walletProvider = window.ethereum
    }
  }
  const provider = new ethers.providers.Web3Provider(walletProvider)
  console.log('cooolpro--', { provider })
  const web3Promise = new Promise(function (resolve, reject) {
    try {
      const loginWallet = localStorage.getItem('loginInfo')
      if (loginWallet) {
        if (!isValidInstace?.isError) {
          const playerContract = new ethers.Contract(
            player1contract,
            player1contractabi,
            provider.getSigner(loginInfo!),
          )
          let tx = null
          if (inputPrams.length > 0) {
            tx = playerContract[methodName](inputPrams.join(), {
              gasLimit: 100000,
            })
          } else {
            tx = playerContract[methodName]()
          }
          resolve(tx)
        } else {
          throw new Error(isValidInstace?.isError)
        }
      } else {
        throw new Error('406')
      }
    } catch (e: any) {
      console.log('WEB3ERR---', e)
      reject(e)
    }
  })
  return web3Promise
}
