import React from 'react'
import classNames from 'classnames'
import '@assets/css/components/Spinner.css'
import { RootState } from '@root/store/rootReducers'
import { useSelector } from 'react-redux'
import { useTranslation } from 'react-i18next'
import approxIcon from '@assets/images/approximation.png'
import dollarIcon from '@assets/images/dollar_mecarreira.png'
import maticIcon from '@assets/images/matic-token-icon.png'
import Spinner from '@components/Spinner'

interface Props {
  logoSet: any
  labelBottom: string
  valueBottom: string
  unit: string
  isInsufficientMatics?: boolean
  isInactive?: boolean
  title: string
  className?: string
  isCryptoLoading?: boolean
  isDisabled?: boolean
  onSelect?: () => void
}

const PaymentMethodCard: React.FC<Props> = props => {
  const { t } = useTranslation()
  const {
    onSelect,
    logoSet,
    isInsufficientMatics,
    labelBottom,
    valueBottom,
    isCryptoLoading = false,
    isDisabled = false,
  } = props
  return (
    <div
      className={classNames(
        'method-card',
        isInsufficientMatics || isDisabled ? 'method-disabled' : '',
      )}
      onClick={onSelect}
    >
      {!isCryptoLoading ? (
        <>
          <div className="h-4">{props?.title}</div>
          <div className="pay-logo-wrapper">
            {logoSet.map((item: any) => (
              <img
                key={item.id}
                src={item?.img}
                className={item.class}
                alt=""
              />
            ))}
          </div>
          {isInsufficientMatics ? (
            <div className="danger-funds">{t('Not enough balance')}</div>
          ) : (
            <div className="pay-details-wrapper">
              <div
                className={classNames(
                  'pay-detail',
                  isDisabled ? 'method-disabled' : '',
                )}
              >
                {labelBottom}
              </div>
              <div
                className={classNames(
                  'pay-detail',
                  isDisabled ? 'method-disabled' : '',
                )}
              >
                <img src={approxIcon} className="approx-icon" alt="" />
                <span>
                  {!isNaN(parseFloat(valueBottom)) ? valueBottom : '00.00'}
                </span>
                <img
                  src={props?.unit === 'matic' ? maticIcon : dollarIcon}
                  className="currency-icon"
                  alt=""
                />
              </div>
            </div>
          )}
        </>
      ) : (
        <div className="spinner-wrapper purchase-loader">
          <Spinner
            className="purchase-spinner"
            spinnerStatus={isCryptoLoading}
            title={t('awaiting Confirmation')}
          />
        </div>
      )}
    </div>
  )
}

export default PaymentMethodCard
