import React, { useEffect } from 'react'
import BottomPopup from '@components/BottomPopup'
import SearchBar from '@components/SearchBar'
import SubmitButton from '@components/SubmitButton'
import ArrowDown from '@components/Svg/ArrowDown'
import ArrowUp from '@components/Svg/ArrowUp'
import { PlayerCardData as PlayerItems } from '@root/constants'
import { NumberFormat } from '@utils/NumberFormat'
import { useState } from 'react'
import { useTranslation } from 'react-i18next'
import NewDraft from './NewDraft'
import CheckCircleOutlinedIcon from '@mui/icons-material/CheckCircleOutline'
import CloseIcon from '@mui/icons-material/Close'
import { useDispatch, useSelector } from 'react-redux'
import {
  fetchDraftNewPlayers,
  getPlayer2Contract,
  getPlayer2ContractReset,
  getPlayerDrafts,
  storeActiveTabBeforeLeaving,
} from '@root/apis/playerCoins/playerCoinsSlice'
import { RootState } from '@root/store/rootReducers'
import DialogBox from '@components/DialogBox'
import { API_CONSTANTS as constants } from '@root/constants'
import { ethers } from 'ethers'
import toast from 'react-hot-toast'
import { callWeb3Method, isMobile, sleep } from '@utils/helpers'
import classNames from 'classnames'
import ResponseAlert from '@components/ResponseAlert'
import { useNavigate, useLocation } from 'react-router-dom'
import Web3ActionPrompt from '../PlayerCoin/web3Actionprompt'

const Drafts = () => {
  const loginInfo = localStorage.getItem('loginInfo')
  const { t } = useTranslation()
  const [selectedFirst, setSelectedFirst] = useState(false)
  const [selectedSecond, setSelectedSecond] = useState(false)
  const [txnErr, setTxnErr] = useState('')
  const [selectedThird, setSelectedThird] = useState(true)
  const [txnHash, setTxnHash] = useState<string>('')
  const [showPrompt, setShowPropmt] = useState<boolean>(false)
  const [showNewDraftPopup, setShowNewDraftPopup] = useState(false)
  const [displayNewDraftPopup, setDisplayNewDraftPopup] = useState(false)
  const [selectedFourth, setSelectedFourth] = useState(false)
  const [isDraftActionInProgress, setDraftActionInProgress] = useState(false)
  const [promptDialog, setPromptDialog] = useState('')
  const [web3Action, setWeb3Action] = useState('')
  const [promptLoading, setPromptLoading] = useState(false)
  const dispatch = useDispatch()
  const navigate = useNavigate()
  const playerCoinData = useSelector((state: RootState) => state.playercoins)
  const {
    isLoading,
    fetchDraftPlayersLoading,
    deployedPlayersList,
    getPlayerDraftsData,
    player2contract,
    player1contract,
    player1contractabi,
    isGetPlayer2ContractSuccess,
    isGetPlayerContractError,
    isGetPlayer1ContractError,
    isGetPlayer2ContractError,
    player2contractabi,
    drafteePlayerUrl,
  } = playerCoinData

  const handleDraftNewPlayer = async () => {
    if (isMobile()) {
      await dispatch(storeActiveTabBeforeLeaving({ activeTab: 'drafts' }))
      navigate('/draft_new_player')
      // setDisplayNewDraftPopup(true)
    } else {
      await dispatch(fetchDraftNewPlayers({ status_id: [3, 4] }))
      // await dispatch(fetchDraftNewPlayers([3, 4]))
      setShowNewDraftPopup(true)
    }
  }

  useEffect(() => {
    dispatch(getPlayerDrafts())
    return () => {
      console.log('outofdraft')
      dispatch(getPlayer2ContractReset())
      // getPlayer2ContractReset
    }
  }, [])

  useEffect(() => {
    if (isGetPlayer2ContractSuccess) {
      setPromptDialog(t('are you sure you want to add this player as draftee'))
      setWeb3Action('addDraftee')
      setPromptLoading(false)
      // if (loginInfo) {
      // buyforExternalWallet()
      // }
      // else if (loginId) {
      //   buyforInternalWallet()
      // }
    }
    if (isGetPlayerContractError) {
      toast.error(t('Loading player contract is failed.'))
    }
    // setIsLoadingMatic(false)
    // setIsDisabledMethod(false)
  }, [isGetPlayer2ContractSuccess, isGetPlayerContractError])

  const buyforExternalWallet = async () => {
    const promise = callWeb3Method(
      // methodName: string,
      'createDraftRequest',
      player1contract, // "0xb6000dffb543008bd7f9cda2615608828f0bf904"
      player1contractabi, // abi of player 1 contract
      [player2contract], // 0xa1eff55865575ddf81c2c5727490524dd56da5e4
    )
    promise
      .then((txn: any) => {
        setTxnHash(txn.hash)
        // setLoading(false)
      })
      .catch((err: any) => {
        // setLoading(false)
        if (err.message === '406') {
          // toast.error(t('this functionality unavailable for internal users'))
          setTxnErr(t('this functionality unavailable for internal users'))
        } else {
          // toast.error(err.reason || err.message)
          setTxnErr(err.reason || err.message)
        }
      })
  }

  const handleDraftClose = (value: any) => {
    console.log('closend')
    if (value?.txnCompleted) {
      dispatch(getPlayerDrafts())
    } else if (value?.playerUrl) {
      console.log('kkk', value?.playerUrl)
      setShowPropmt(true)
      setPromptLoading(true)
      dispatch(getPlayer2Contract(value?.playerUrl))
    } else if (!value?.playerUrl) {
      setShowPropmt(false)
    }
    setShowNewDraftPopup(false)
  }

  useEffect(() => {
    if (drafteePlayerUrl) {
      navigate('/draft_confirmation')
      setPromptLoading(true)
      dispatch(getPlayer2Contract(drafteePlayerUrl))
    }
  }, [drafteePlayerUrl])

  const handleDeleteDraftee = (drafteeAddress: string) => {
    setDraftActionInProgress(true)
    const promise = callWeb3Method(
      // methodName: string,
      'deleteDraftee',
      player1contract,
      player1contractabi,
      [drafteeAddress],
    )
    promise
      .then((txn: any) => {
        console.log('txnHash is----', txn)
        setTxnHash(txn.hash)
      })
      .catch((err: any) => {
        console.log('txnHash errro----', err.reason)
        toast.error(err.reason)
      })
  }

  const onCloseBottomPopup = () => {
    console.log('OCBP')
    setTxnHash('')
    setDraftActionInProgress(false)
    dispatch(getPlayerDrafts())
  }

  const handleAcceptDraftee = (drafteeAddress: string) => {
    setDraftActionInProgress(true)
    const promise = callWeb3Method(
      // methodName: string,
      'acceptDraftRequest',
      player1contract,
      player1contractabi,
      [drafteeAddress],
    )
    promise
      .then((txn: any) => {
        console.log('txnHash is----', txn)
        setTxnHash(txn.hash)
      })
      .catch((err: any) => {
        setDraftActionInProgress(false)
        if (err.message === '406') {
          toast.error(t('this functionality unavailable for internal users'))
        } else {
          toast.error(err.reason || err.message)
        }
        // toast.error(err.reason || err.message)
      })
  }

  const getRequestParams = () => {
    const { adminAccount } = inputData
    if (web3Action === 'addAdmin') {
      return adminAccount
    }
    return ''
  }

  const handleActionSuccess = async () => {
    console.log({ web3Action })
    buyforExternalWallet()

    // for (let i = 0; i < 2; i++) {
    //   await sleep(i * 1000)
    // }
    // const reqparam: string = getRequestParams()
    // const promise = callWeb3Method(
    //   // methodName: string,
    //   'setPlayerRole',
    //   player1contract,
    //   player1contractabi,
    //   [reqparam], //[adminAccount],
    // )
    // promise
    //   .then((txn: any) => {
    //     setTxnHash(txn.hash)
    //     // handleClearInput('adminAccount')
    //     // setAdminActionInProgress(false)
    //   })
    //   .catch((err: any) => {
    //     // handleClearInput('adminAccount')
    //     // setAdminActionInProgress(false)
    //     if (err.message === '406') {
    //       // toast.error(t('this functionality unavailable for internal users'))
    //       setTxnErr(t('this functionality unavailable for internal users'))
    //     } else {
    //       // toast.error(err.reason || err.message)
    //       setTxnErr(err.reason || err.message)
    //     }
    //   })
  }

  const handleRemoveAdminPrompt = () => {
    setShowPropmt(false)
    setTxnErr('')
    setTxnHash('')
    setPromptDialog('')
    setWeb3Action('')
  }

  return (
    <div className="player-draft dlg-content">
      <DialogBox
        isOpen={showNewDraftPopup || showPrompt}
        onClose={handleDraftClose}
        contentClass="onboarding-popup"
      >
        {showNewDraftPopup ? (
          <NewDraft onClose={handleDraftClose} />
        ) : showPrompt ? (
          <Web3ActionPrompt
            txnHash={txnHash}
            isPromptLoading={promptLoading}
            errorMsg={txnErr}
            promptText={promptDialog}
            onSuccess={handleActionSuccess}
            onClose={handleRemoveAdminPrompt}
            walletAddress={player2contract}
            operationMode={web3Action === 'addDraftee' ? 'add' : 'remove'}
          />
        ) : null}
      </DialogBox>
      <>
        {/* {
          <BottomPopup
            mode="wallet drafting-players-loader"
            isOpen={isDraftActionInProgress || txnHash.length > 0}
          >
            {isDraftActionInProgress && !txnHash ? (
              <div className="draft-bottompop">
                <div className="checkout-loader-wrapper">
                  <div className="loading-spinner">
                    <div className="spinner__circle">
                      <div className="spinner__circle-gradient"></div>
                      <div className="spinner__circle-inner"></div>
                    </div>
                  </div>
                </div>
              </div>
            ) : (
              <>
                <div className="txnhash-success-wrapper">
                  <ResponseAlert status={'Success'} />
                  {txnHash && (
                    <a
                      className="tx-link"
                      href={'https://mumbai.polygonscan.com/tx/' + txnHash}
                      target="_blank"
                    >
                      {t('show transaction')}
                    </a>
                  )}
                </div>
                <div className="bottom-close">
                  <div className="close-button" onClick={onCloseBottomPopup}>
                    {t('close')}
                  </div>
                </div>
              </>
            )}
          </BottomPopup>
        } */}
        <>
          {getPlayerDraftsData?.active_draft_player ? (
            <>
              {/* <BottomPopup mode="wallet" isOpen={showNewDraftPopup}>
            <NewDraft onClose={handleDraftClose} />
            </BottomPopup> */}
              {/* <DialogBox
            isOpen={showNewDraftPopup}
            onClose={handleDraftClose}
            contentClass="onboarding-popup"
            > */}
              {/* <NewDraft onClose={handleDraftClose} /> */}
              {/* </DialogBox> */}
              {/* <SearchBar
            onEdit={handleSearch}
          containerClass="drafts-search-bar"
          onClose={() => console.log('')}
          /> */}
              <div className="accordion mt-0">
                <div className="item">
                  {/* <div className="title">
            <h2>{t('draft requests')} (1,345)</h2>
          </div> */}
                  <div
                    className={selectedThird ? 'title no-border' : 'title'}
                    onClick={() => setSelectedThird(!selectedThird)}
                  >
                    {/* <h2>{t('draft requests')} (1,345)</h2> */}
                    <h2>{t('draft requests')}</h2>
                    {selectedThird ? <ArrowUp /> : <ArrowDown />}
                  </div>
                  <div className={selectedThird ? 'content show' : 'content'}>
                    {getPlayerDraftsData?.requested_by?.length > 0 ? (
                      getPlayerDraftsData?.requested_by?.map(
                        (item: any, index: number) => (
                          <div className="nft-item" key={index}>
                            <div className="nft-image-section">
                              <div className="image-border">
                                <img
                                  loading="lazy"
                                  // src={item.img.default}
                                  src={`${constants.HOST_URL}/meCarreira_backend/media/${item.playerpicture}`}
                                  alt=""
                                  className="nft-image"
                                />
                              </div>
                              <div className="nft-name">{item.name}</div>
                            </div>
                            <div className="price-button-section">
                              <div className="nft-price-section">
                                <div className="white-color">
                                  {/* {NumberFormat(item.price)} */}
                                </div>
                                {/* <div className="nft-price">${NumberFormat(item.price)}</div> */}
                              </div>
                              <CheckCircleOutlinedIcon
                                className="green-color"
                                onClick={() =>
                                  handleAcceptDraftee(item.draftrequestby)
                                }
                              />
                            </div>
                          </div>
                        ),
                      )
                    ) : (
                      <div className="drafts-no-action-container">
                        <div>{t('no action yet')}</div>
                      </div>
                    )}
                  </div>
                </div>
                <div className="item">
                  {/* <div className="title">
            <h2>{t('draft requests')} (1,345)</h2>
          </div> */}
                  <div
                    className={selectedFourth ? 'title no-border' : 'title'}
                    onClick={() => setSelectedFourth(!selectedFourth)}
                  >
                    {/* <h2>{t('draft requests')} (1,345)</h2> */}
                    <h2>{t('drafted by')}</h2>
                    {selectedFourth ? <ArrowUp /> : <ArrowDown />}
                  </div>
                  <div className={selectedFourth ? 'content show' : 'content'}>
                    {getPlayerDraftsData?.drafted_by?.length > 0 ? (
                      getPlayerDraftsData?.drafted_by.map(
                        (item: any, index: number) => (
                          <div className="nft-item" key={index}>
                            <div className="nft-image-section">
                              <div className="image-border">
                                <img
                                  loading="lazy"
                                  // src={item.img.default}
                                  src={`${constants.HOST_URL}/meCarreira_backend/media/${item.playerpicture}`}
                                  alt=""
                                  className="nft-image"
                                />
                              </div>
                              <div className="nft-name">{item.name}</div>
                            </div>
                            <div className="price-button-section">
                              <div className="nft-price-section">
                                <div className="white-color">
                                  {/* {NumberFormat(item.price)} */}
                                </div>
                                {/* <div className="nft-price">${NumberFormat(item.price)}</div> */}
                              </div>
                              {/* <CheckCircleOutlinedIcon className="green-color" /> */}
                            </div>
                          </div>
                        ),
                      )
                    ) : (
                      <div className="drafts-no-action-container">
                        <div>{t('no action yet')}</div>
                      </div>
                    )}
                  </div>
                </div>
                <div className="item" key={1}>
                  <div
                    className={selectedFirst ? 'title no-border' : 'title'}
                    onClick={() => setSelectedFirst(!selectedFirst)}
                  >
                    <h2>
                      {t('active drafted')} (
                      {getPlayerDraftsData?.active_draft_player.length}/5)
                    </h2>
                    {selectedFirst ? <ArrowUp /> : <ArrowDown />}
                  </div>
                  <div className={selectedFirst ? 'content show' : 'content'}>
                    {getPlayerDraftsData?.active_draft_player.length > 0 ? (
                      <div className="list-header">
                        <div>{t('address')}</div>
                        {/* <div>{t('amount drafted')}</div> */}
                      </div>
                    ) : null}
                    {getPlayerDraftsData?.active_draft_player.length > 0 ? (
                      getPlayerDraftsData?.active_draft_player.map(
                        (item: any, index: number) => (
                          <div className="nft-item" key={index}>
                            <div className="nft-image-section">
                              <div className="image-border">
                                <img
                                  loading="lazy"
                                  // src={item.img.default}
                                  src={`${constants.HOST_URL}/meCarreira_backend/media/${item.playerpicture}`}
                                  alt=""
                                  className="nft-image"
                                />
                              </div>
                              <div className="nft-name">{item.name}</div>
                            </div>
                            <div className="price-button-section">
                              <div className="nft-price-section">
                                <div className="white-color">
                                  {/* {NumberFormat(item.price)} */}
                                </div>
                                {/* <div className="nft-price">${NumberFormat(item.price)}</div> */}
                              </div>
                              <CloseIcon
                                className="red-color"
                                onClick={() =>
                                  handleDeleteDraftee(item.draftedplayer)
                                }
                              />
                            </div>
                          </div>
                        ),
                      )
                    ) : (
                      <div className="drafts-no-action-container">
                        <div>{t('no action yet')}</div>
                      </div>
                    )}
                    <div className="mb-20"></div>
                    <SubmitButton
                      isDisabled={
                        getPlayerDraftsData?.active_draft_player.length === 5
                      }
                      title={t('draft new player')}
                      // onPress={() => setShowNewDraftPopup(true)}
                      onPress={() => handleDraftNewPlayer()}
                      className="m-0auto"
                    />
                  </div>
                </div>
                <div className="item" key={2}>
                  <div
                    className="title"
                    onClick={() => setSelectedSecond(!selectedSecond)}
                  >
                    <h2>{t('drafted in the past')}</h2>
                    {selectedSecond ? <ArrowUp /> : <ArrowDown />}
                  </div>
                  <div className={selectedSecond ? 'content show' : 'content'}>
                    {getPlayerDraftsData?.all_drafted_player?.length > 0 ? (
                      getPlayerDraftsData?.all_drafted_player?.map(
                        (item: any, index: number) => (
                          <div className="nft-item" key={index}>
                            <div className="nft-image-section">
                              <div className="image-border">
                                <img
                                  loading="lazy"
                                  // src={item.img.default}
                                  src={`${constants.HOST_URL}/meCarreira_backend/media/${item.playerpicture}`}
                                  alt=""
                                  className="nft-image"
                                />
                              </div>
                              <div className="nft-name">{item.name}</div>
                            </div>
                            <div className="nft-price-section">
                              {/* <div className="white-color">{NumberFormat(item.price)}</div> */}
                              {/* <div className="nft-price">${NumberFormat(item.price)}</div> */}
                            </div>
                          </div>
                        ),
                      )
                    ) : (
                      <div className="drafts-no-action-container">
                        <div>{t('no action yet')}</div>
                      </div>
                    )}
                  </div>
                </div>
              </div>
              <div className="mb-20"></div>
              {/* {!selectedFirst && (
            <SubmitButton
              isLoading={fetchDraftPlayersLoading}
              isDisabled={false}
              title={t('draft new player')}
              onPress={() => handleDraftNewPlayer()}
              className="m-0auto"
            />
          )} */}
            </>
          ) : (
            <div className="checkout-loader-wrapper">
              <div className="loading-spinner">
                <div className="spinner__circle">
                  <div className="spinner__circle-gradient"></div>
                  <div className="spinner__circle-inner"></div>
                </div>
              </div>
            </div>
          )}
        </>
      </>
    </div>
  )
}

export default Drafts
