import * as React from 'react'
import Button from '@mui/material/Button'
import ClickAwayListener from '@mui/material/ClickAwayListener'
import Grow from '@mui/material/Grow'
import Paper from '@mui/material/Paper'
import Popper from '@mui/material/Popper'
import MenuItem from '@mui/material/MenuItem'
import MenuList from '@mui/material/MenuList'
import Stack from '@mui/material/Stack'

interface PopperProps {
  isOpen: boolean
  isDisabled?: boolean
}

// export default function PopperMenu<PopperProps>({ isOpen, isDisabled }) {
const PopperMenu: React.FC<PopperProps> = ({ isOpen, isDisabled }) => {
  const [open, setOpen] = React.useState(false)
  const anchorRef: any = React.useRef(null)

  const handleToggle = () => {
    setOpen(prevOpen => !prevOpen)
  }

  const handleClose = (event: any) => {
    if (anchorRef.current && anchorRef.current.contains(event.target)) {
      return
    }

    setOpen(false)
  }

  function handleListKeyDown(event: any) {
    if (event.key === 'Tab') {
      event.preventDefault()
      setOpen(false)
    } else if (event.key === 'Escape') {
      setOpen(false)
    }
  }

  // return focus to the button when we transitioned from !open -> open
  const prevOpen = React.useRef(open)
  React.useEffect(() => {
    if (prevOpen.current === true && open === false) {
      anchorRef.current.focus()
    }

    prevOpen.current = open
  }, [open])

  return (
    <Stack direction="row" spacing={2} style={{ position: 'relative' }}>
      <Popper
        open={isOpen}
        anchorEl={anchorRef.current}
        role={undefined}
        placement="bottom-start"
        transition
        disablePortal
        style={{ position: 'absolute', zIndex: 100, width: '100px' }}
      >
        {({ TransitionProps, placement }) => (
          <Grow
            {...TransitionProps}
            style={{
              transformOrigin:
                placement === 'bottom-start' ? 'left top' : 'left bottom',
            }}
          >
            <Paper
              style={{
                backgroundColor: '#212435',
                paddingLeft: '10px',
                '-webkit-box-shadow': '-5px 7px 15px 0px rgba(0,0,0,0.75)',
                '-moz-box-shadow': '-5px 7px 15px 0px rgba(0,0,0,0.75)',
                'box-shadow': '-5px 7px 15px 0px rgba(0,0,0,0.75)',
              }}
            >
              <ClickAwayListener onClickAway={handleClose}>
                <MenuList
                  autoFocusItem={open}
                  id="composition-menu"
                  aria-labelledby="composition-button"
                  onKeyDown={handleListKeyDown}
                >
                  <MenuItem
                    onClick={handleClose}
                    disabled={isDisabled}
                    style={{
                      color: '#fff',
                      display: 'table-cell',
                      'font-family': 'Rajdhani-regular',
                      'font-weight': 400,
                      'font-size': '16px',
                    }}
                  >
                    Send
                  </MenuItem>
                </MenuList>
              </ClickAwayListener>
            </Paper>
          </Grow>
        )}
      </Popper>
    </Stack>
  )
}

export default PopperMenu
