import React, { useEffect, useState } from 'react'
import SearchBar from '@components/SearchBar'
import NftCard from '../../../components/Card/NftCard'
import { useDispatch, useSelector } from 'react-redux'
import { fetchMintedNFTList } from '@root/apis/gallery/gallerySlice'
import { RootState } from '@root/store/rootReducers'
import { useTranslation } from 'react-i18next'
import classNames from 'classnames'

const NftList = () => {
  const dispatch = useDispatch()
  const { t } = useTranslation()
  const [loaded, setLoaded] = useState(false)

  const handleSearch = (value: string | undefined) => {
    console.log('searchKeys---', value)
  }

  const { isLoading, mintedNFTList } = useSelector(
    (state: RootState) => state.gallery,
  )

  useEffect(() => {
    dispatch(fetchMintedNFTList())
    setLoaded(true)
  }, [])

  return (
    <>
      {/* <SearchBar onEdit={handleSearch} onClose={() => console.log('')} /> */}
      {isLoading || !loaded ? (
        <div
          className={classNames(
            'loading-spinner-container mb-40 mt-40',
            isLoading ? 'show' : '',
          )}
        >
          <div className="loading-spinner">
            <div className="spinner__circle">
              <div className="spinner__circle-gradient"></div>
              <div className="spinner__circle-inner"></div>
            </div>
          </div>
        </div>
      ) : (
        <>
          {mintedNFTList.length > 0 ? (
            <div className="nft-tab-title">{t('launched NFTs')}</div>
          ) : (
            <div className="nft-tab-title yellow-color">
              {t('no NFTs launched')}
            </div>
          )}
          <div className="nft-line">
            <div className="nft-column">
              {mintedNFTList.map((item: any, index: any) => {
                return (
                  index % 2 === 0 && (
                    <NftCard
                      nft={{
                        title: item.name,
                        img: {
                          default: item.artwork_thumb,
                        },
                      }}
                      key={index}
                    />
                  )
                )
              })}
            </div>
            <div className="nft-column">
              {mintedNFTList &&
                mintedNFTList.map((item: any, index: any) => {
                  return (
                    index % 2 === 1 && (
                      <NftCard
                        nft={{
                          title: item.name,
                          img: {
                            default: item.artwork_thumb,
                          },
                        }}
                        key={index}
                      />
                    )
                  )
                })}
            </div>
          </div>
        </>
      )}
    </>
  )
}

export default NftList
