import React from 'react'
import { useTranslation } from 'react-i18next'
import '@assets/css/components/NftGalleryCard.css'
import { NFT_STATUS } from '@root/constants'
interface Props {
  img: any
  className?: string
  status: number
  onClick?: () => any
  onLaunch?: () => any
}

const NftGalleryCard: React.FC<Props> = ({
  img,
  className,
  status,
  onClick,
  onLaunch,
}) => {
  const { t } = useTranslation()

  const handleLaunch = (event: any) => {
    event.stopPropagation()
    onLaunch && onLaunch()
  }

  return (
    <div className={`nft-gallery-card ${className}`} onClick={onClick}>
      <img src={img} className="nft-gallery-img" />
      {status === NFT_STATUS.INITIAL ? (
        // <CheckCircle className="nft-gallery-firsticon" />
        <></>
      ) : status === NFT_STATUS.LAUNCHED ? (
        // <Error className="nft-gallery-secondicon" />
        <></>
      ) : status === NFT_STATUS.PUBLIC ? (
        <>
          {/* <div className="nft-gallery-thirdicon">
            <FiberManualRecord className="nft-gallery-thirdicondot" />
          </div> */}
          <div className="nft-gallery-lanchbtn" onClick={handleLaunch}>
            {t('launch')}
          </div>
        </>
      ) : (
        <></>
      )}
    </div>
  )
}

export default NftGalleryCard
