import React, { useState, useEffect } from 'react'
import { PlayerCardData as PlayerItems, PLAYER_STATUS } from '@root/constants'
import SearchBar from '@components/SearchBar'
import PlayerItem from './PlayerItem'
import DialogBox from '@components/DialogBox'
import StakedForm from './StakedForm'
import { isMobile } from '@utils/helpers'
import { useNavigate } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import {
  fetchPlayersBalance,
  // fetchPlayersStats,
} from '@root/apis/playerCoins/playerCoinsSlice'
import { RootState } from '@root/store/rootReducers'
import { useTranslation } from 'react-i18next'
import classNames from 'classnames'
import { getUsdRate } from '@root/apis/purchase/purchaseSlice'
import {
  fetchPlayerCoinStats,
  fetchPlayersStats,
} from '@root/apis/playerStats/playerStatsSlice'
import BottomPopup from '@components/BottomPopup'

let intervalId: any = null
interface PlayerOffset {
  start: number
  end: number
}

const PlayerCoins: React.FC = () => {
  const [showFormPopup, setShowFormPopup] = useState(false)
  const [hasCompleted, setHasCompleted] = useState(false)
  const [optedId, setOptedId] = useState(NaN)
  const [traverseTokens, setTraverseTokens] = useState([])
  const navigate = useNavigate()
  const dispatch = useDispatch()
  const loginInfo = localStorage.getItem('loginInfo')
  const playerCoinData = useSelector((state: RootState) => state.playercoins)
  const playerStatsData = useSelector((state: RootState) => state.playerstats)
  const {
    fetchPlayerCoinStatsError,
    fetchPlayerCoinStatsData,
    fetchPlayerCoinStatsRateData,
  } = playerStatsData
  const { t } = useTranslation()
  const {
    fetchBalancePlayersProgress,
    fetchBalancePlayersSuccess,
    fetchBalancePlayersData,
    fetchBalancePlayersError,
    isStakingOnlySelected,
  } = playerCoinData
  const purchaseTransactionData = useSelector(
    (state: RootState) => state.purchases,
  )
  const authenticationData = useSelector(
    (state: RootState) => state.authentication,
  )
  const {
    loader,
    passphraseLoader,
    userWalletData: { USDBalance, address, balance, exchangeRateData },
  } = authenticationData
  const { usdRate } = purchaseTransactionData
  const { token = [], NFT = [], matic = [] } = fetchBalancePlayersData

  const handleClose = (event: any) => {
    event.stopPropagation()
    setShowFormPopup(false)
  }

  const handleStake = (item: any) => {
    if (isMobile()) {
      navigate('/staked')
    } else {
      setShowFormPopup(true)
    }
  }
  const arrs: any = []
  const handleSearch = (value: string) => {
    const traverseTemp: any = []
    let i: number
    const filter = value.toUpperCase()

    for (i = 0; i < token.length; i++) {
      if (token[i].name.toUpperCase().indexOf(filter) > -1) {
        traverseTemp.push(token[i])
      } else {
        traverseTemp.filter((item: any) => item.name === token[i].name)
      }
    }
    setTraverseTokens(traverseTemp)
  }

  const handleGetPriceStats = () => {
    console.log({ fetchPlayerCoinStatsData })
    const playersSet: number[] = token
      .filter((player: { playerstatusid: { id: number } }) => {
        return player.playerstatusid.id > 2
      })
      .map((item: any) => item.id)
    console.log({ playersSet })
    if (playersSet.length > 0) {
      dispatch(fetchPlayerCoinStats(playersSet))
    }
  }

  useEffect(() => {
    console.log({
      fetchPlayerCoinStatsData,
      traverseTokens,
      fetchBalancePlayersError,
    })
  }, [fetchPlayerCoinStatsData, traverseTokens, fetchBalancePlayersError])

  useEffect(() => {
    // const reqParams = {
    //   address: loginInfo || address,
    // }
    // dispatch(fetchPlayersBalance(reqParams))
    dispatch(getUsdRate())
    // intervalId = setInterval(() => {
    //   dispatch(getUsdRate())
    // }, 5000)
    return () => clearInterval(intervalId)
  }, [])

  useEffect(() => {
    const reqParams = {
      address: loginInfo || address,
    }
    clearInterval(intervalId)
    dispatch(fetchPlayersBalance(reqParams))
  }, [isStakingOnlySelected])

  useEffect(() => {
    console.log({ fetchBalancePlayersProgress, fetchBalancePlayersSuccess })
  }, [playerCoinData])

  useEffect(() => {
    if (token.length > 0) {
      if (!hasCompleted) {
        handleGetPriceStats()
        setHasCompleted(true)
      }
      console.log('loop_running')
      // handleGetPriceStats()
      intervalId = setInterval(() => {
        handleGetPriceStats()
      }, 10000)
      setTraverseTokens(token)
    }
  }, [token])

  const onMoreClick = (checked: boolean, id: number) => {
    if (checked) {
      setOptedId(id)
    } else {
      setOptedId(NaN)
    }
  }

  const getStatData = (id: number) => {
    const index = fetchPlayerCoinStatsData.findIndex(
      (item: any) => Number(item.id) === id,
    )
    console.log('kkk--', { index, fetchPlayerCoinStatsData, id })
    return {
      coin_issued: fetchPlayerCoinStatsData[index]?.coin_issued,
      matic: fetchPlayerCoinStatsData[index]?.matic,
      usd_rate: fetchPlayerCoinStatsRateData?.rate,
    }
  }

  const handleContainerClick = (evt: any) => {
    evt.preventDefault()
    setOptedId(NaN)
  }

  return (
    <>
      <SearchBar
        isSwitchEnabled={true}
        mode="wallet"
        onEdit={handleSearch}
        onClose={() => handleSearch('')}
      />
      {/* <DialogBox
        isOpen={showFormPopup}
        onClose={handleClose}
        contentClass=""
        closeBtnClass="close-purchase"
      >
        <StakedForm />
      </DialogBox> */}
      <BottomPopup mode="wallet drafting-players-loader" isOpen={showFormPopup}>
        <StakedForm />
      </BottomPopup>
      <div
        className={classNames(
          'dlg-content border-top m-0 fixed-content',
          // fetchBalancePlayersProgress ? 'fixed-content' : '',
        )}
        style={{ height: isMobile() ? '649px' : '83%' }}
        onClick={handleContainerClick}
      >
        {/* {PlayerItems.map((item, index) => (
          <PlayerItem
            item={item}
            key={index}
            index={index}
            handleStake={handleStake}
          />
        ))} */}
        {traverseTokens.length > 0 && fetchPlayerCoinStatsData.length > 0 ? (
          traverseTokens.map((item: any, index: number) => (
            <PlayerItem
              item={item}
              key={index}
              index={index}
              statData={getStatData(item.id)}
              handleStake={() => handleStake(item)}
              usdRate={usdRate}
              handleMoreClick={checked => onMoreClick(checked, index)}
              isOpted={optedId === index}
            />
          ))
        ) : (
          <div
            className="checkout-loader-wrapper"
            style={{ height: '100%', overflowX: 'hidden' }}
          >
            {((fetchBalancePlayersProgress && !fetchBalancePlayersSuccess) ||
              !fetchPlayerCoinStatsData.length) &&
            !fetchBalancePlayersError ? (
              <>
                {fetchBalancePlayersSuccess && traverseTokens.length === 0 ? (
                  <div className="drafts-no-action-container no-new-draft">
                    <div>{t('no results found')}</div>
                  </div>
                ) : (
                  <div className="loading-spinner">
                    <div className="spinner__circle">
                      <div className="spinner__circle-gradient"></div>
                      <div className="spinner__circle-inner"></div>
                    </div>
                  </div>
                )}
              </>
            ) : (
              <div className="drafts-no-action-container no-new-draft">
                <div>{t('no results found')}</div>
              </div>
            )}
          </div>
        )}
      </div>
    </>
  )
}

export default PlayerCoins
